<template>
	<div>
		<el-table
    ref="singleTable"
    :data="tableData"
    highlight-current-row
    style="width: 100%">
    <el-table-column
      type="index"
      width="50">
    </el-table-column>
    <el-table-column
      property="title"
      label="Title"
      width="120">
    </el-table-column>
    <el-table-column
      property="author"
      label="Author"
      width="120">
    </el-table-column>
    <el-table-column
      property="pageviews"
      label="pageviews">
    </el-table-column>
    <el-table-column
        property="display_time"
        label="display_time">
    </el-table-column>
    <el-table-column
        label="status">
        <template slot-scope='scope'>
          <el-tag :type='scope.row.status|filtermessage'>{{scope.row.status}}</el-tag>
        </template>

    </el-table-column>

  </el-table>
	</div>
</template>
<script>
  import {getList} from '@/api/table.js'
  export default {
    data() {
      return {
        currentRow: null,
        tableData:[]
      }
    },
    created(){
      this.fetchData()
    },
    filters:{
        filtermessage(value){
          const datavalue={
            "publish":"success",
             "draft":'info',
            "deleted":'primary'
          }
          return datavalue[value]
        }
    },
    methods: {
    	fetchData(){
    		 getList(this.listQuery).then(res=>{
         this.tableData=res.data.items
         console.log(res.data.items)
         })
    	}
    }
  }
</script>


